.tran-mau-tri {
    font-weight: 600;
    display: inline-block;
    width: 192px;
    flex-shrink: 0;
}

.free1,
.tran-mau-tri {
    position: relative;
    letter-spacing: -0.02em;
    line-height: 24px;
}

.free {
    border-radius: var(--br-5xs);
    background-color: var(--primary-02);
    overflow: hidden;
    display: flex;
    flex-direction: row;
    padding: 0 var(--padding-xs);
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: var(--caption-1-semibold-size);
    color: var(--neutral-07-100);
}

.tran-mau-tri-tam-parent {
    align-self: stretch;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
}

.tamui8net {
    align-self: stretch;
    position: relative;
    font-size: var(--caption-1-semibold-size);
    letter-spacing: -0.02em;
    line-height: 24px;
    font-weight: 600;
    color: var(--neutral-03-50);
    display: flex;
    align-items: center;
    height: 16px;
    flex-shrink: 0;
}

.frame-parent3,
.property-1default {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

.frame-parent3 {
    flex: 1;
    flex-direction: column;
}

.property-1default {
    border-radius: var(--br-xs);
    width: 264px;
    overflow: hidden;
    flex-direction: row;
    padding: var(--padding-3xs);
    box-sizing: border-box;
    gap: var(--gap-base);
    text-align: left;
    font-size: var(--base-2-size);
    color: var(--neutral-01-100);
    font-family: var(--inter);
}
