.online-dot-child {
    border-radius: var(--br-21xl);
    background-color: var(--primary-02);
    border: 4px solid var(--neutral-06-100);
    box-sizing: border-box;
    width: 18px;
    height: 18px;
    overflow: hidden;
    flex-shrink: 0;
}

.online-dot {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 12px;
    height: 12px;
    display: flex;
    flex-direction: row;
    padding: var(--padding-11xs);
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
}

.avataronline {
    width: 40px;
    height: 40px;
}
